                <!--{{dynamic.isConcern===0?'+关注':'已关注'}}-->
                <!--</button>-->
            </div>
            <pre class="dynamic-text" v-html="dynamic.title"></pre>
            <ul class="dynamic-images">
                <template v-if="(dynamic.type===0||dynamic.type===1)&&(dynamic.url.length === 1)">
                    <li @click="previewImage(index)" class="image-item image-item1"
                        v-for="(item,index) in dynamic.url"
                        :key="index"
                        :style="{background: 'url('+item+')center no-repeat',backgroundSize: 'cover'}"></li>
                </template>
                <template v-if="((dynamic.url.length === 2)||(dynamic.url.length === 4))&&(dynamic.type===2)">
                    <li @click="previewImage(index)" class="image-item image-item2"
                        v-for="(item,index) in dynamic.url"
                        :key="index"
                        :style="{background: 'url('+item+')center no-repeat',backgroundSize: 'cover'}"></li>
                </template>
                <template v-if="(dynamic.url.length > 4)||(dynamic.url.length === 3)">
                    <li @click="previewImage(index)" class="image-item image-item3"
                        v-for="(item,index) in dynamic.url"
                        :key="index"
                        :style="{background: 'url('+item+')center no-repeat',backgroundSize: 'cover'}"></li>
                </template>
                <template v-if="(dynamic.url.length)&&(dynamic.type===3)">
                    <li class="player">
                        <div class="player-box full">
                            <div class="display">
                                <video
                                        muted
                                        controls
                                        webkit-playsinline
                                        playsinline
                                        x5-playsinline
                                        x-webkit-airplay='allow'
                                        crossorigin="anonymous"
                                        width="100%"
                                        height="100%"
                                        preload="auto"
                                        class="show"
                                        style="display: inline;"
                                        :src="dynamic.url[0]"
                                >您的浏览器不支持video
                                </video>
                                <div class="load-layer" v-show="loadLayer" @click.stop>
                                    <!--<img crossOrigin="anonymous"-->
                                    <!--:src="vPoster">-->
                                    <div :style="{width:'100%',height:'100%',background:'url('+vPoster+')center no-repeat','backgroundSize':'cover'}"></div>
                                    <i class="player-icon icon-preview iconfont icon-bofang"
