                                </div>
                            </div>
                        </div>
                    </li>
                </template>
                <template v-if="(dynamic.url.length)&&(dynamic.type===4)">
                    <li class="player column-video">
                        <div class="player-box full">
                            <div class="display">
                                <video
                                        muted
                                        controls
                                        webkit-playsinline
                                        playsinline
                                        x5-playsinline
                                        x-webkit-airplay='allow'
                                        crossorigin="anonymous"
                                        width="100%"
                                        height="100%"
                                        preload="auto"
                                        class="show"
                                        style="display: inline;"
                                        :src="dynamic.url[0]"
                                >您的浏览器不支持video
                                </video>
                                <div class="load-layer" v-show="loadLayer" @click.stop>
                                    <!--<img crossOrigin="anonymous"-->
                                    <!--:src="vPoster">-->
                                    <div :style="{width:'100%',height:'100%',background:'url('+vPoster+')center no-repeat','backgroundSize':'cover'}"></div>
                                    <i class="player-icon icon-preview iconfont icon-bofang"
                                       @click.stop="playVideo"></i>
                                </div>
                            </div>
                        </div>
                    </li>
                </template>
                <template v-if="dynamic.type===7">
                    <li class="share-link">
                        <img class="share-img" :src="dynamic.url[0]" alt="">
                        <div class="share-text">{{dynamic.url[1]}}</div>
                    </li>
                </template>
