                        <span class="comment-count">{{dynamic.commentNum}}</span>
                    </div>
                    <div class="tab-item tab-thumb-up" :class="{cur:tabIndex===1}" @click="changeTab(1)">
                        <i>赞<em></em></i>
                        <span class="thumb-up-count">{{dynamic.zambiaNum}}</span>
                    </div>
                    <div class="comment-sort">
