                            <i class="arrow"></i>
                            <p @click="checkedSort(0)" class="by-hot" :class="{'checked-style':sortIndex===0}">
                                按热度</p>
                            <p @click="checkedSort(1)" class="by-date" :class="{'checked-style':sortIndex===1}">
                                按时间</p>
                        </div>
                        <div class="sort-style-box" @click="checkedSort" @touchmove.prevent v-show="changeSort"></div>
                    </div>
                </div>
                <section class="tab-content" :class="{'paddingContent':tabFixed}">
                    <ul v-if="tabIndex === 0" class="comment-list">
                        <li class="defalut-page" v-if="comment.length===0">
                            <default-page :img-url="require('../../assets/images/default-comment.png')"></default-page>
                        </li>
