                                <p class="p-nickname">{{item.userName?item.userName:'匿名用户'}}</p>
                                <div class="detail-text">{{item.content}}</div>
                                <div class="detail-dialogue" v-if="item.embeddedReply !== null" @click="$isApp">
                                    <template v-for="replyItem in (item.embeddedReply.replies)">
                                        <p>
                                            <span class="blue-font">{{replyItem.userName?replyItem.userName:'匿名用户'}}</span>:<span>{{replyItem.content}}</span>
                                        </p>
                                    </template>
                                    <p class="total-reply blue-font" v-if="item.replyNum!==null">
                                        <span to="/replyDetail" class="blue-font">共{{item.replyNum}}条回复>>
                                        </span>
                                    </p>
                                </div>
                                <div class="comment-operate" @click="$isApp">
                                    <div class="operate operate-time">{{$formatTime(new Date(item.createTime))}}</div>
                                    <div class="operate operate-like">{{item.zambiaNum===null?0:item.zambiaNum}}</div>
                                    <div class="operate operate-write"></div>
                                </div>
                            </div>
                            <div class="clearfix"></div>
                        </li>
                        <li v-if="comment.length!==0">
                            <div class="app-see-more" @click="$isApp()">进入App查看更多</div>
                        </li>
                    </ul>
                    <ul v-else-if="tabIndex === 1" class="thumb-up-list">
                        <li class="defalut-page" v-if="likeList.length===0">
                            <default-page :img-url="require('../../assets/images/default-thumb.png')"></default-page>
                        </li>
                        <li class="thumb-up-item" v-for="(item,index) in likeList.list" :key="index">
                            <img :src="item.userHeadUrl" alt="">
