                                <p class="thumb-up-nickname">{{item.userName}}</p>
                                <p class="thumb-createtime">{{$formatTime(new Date(item.createTime))}}</p>
                            </div>
                        </li>
                    </ul>
                </section>
            </div>
        </div>
        <!--<div class="footer-input">-->
        <!--<input type="text" placeholdxer="评论点什么~~~">-->
        <!--<div class="count like-count">200</div>-->
        <!--<div class="count trans-count">200</div>-->
        <!--</div>-->
    </div>
</template>

<script>
  import { ImagePreview } from 'vant'
  import tipOffs from '../tipOffs/TipOffs'
  import defaultPage from '../../components/DafultPage'

  export default {
    name: "detailActivities",
    components: {
      tipOffs,
      defaultPage
    },
    data() {
      return {
        loadLayer: true,// 视频图片
        changeSort: false, // 更改排序方式
        sortStyle: '按热度', // 排序方式
        sortIndex: 0,// 默认选项下标
        tabIndex: 0,// tab下标
        tipOffsYes: true,
        tabFixed: false,
        likeList: [],
        dynamic: {
          account: "",
          browseNum: 0,
          collectNum: 0,
          commentNum: 0,
          createTime: 0,
          forwardId: null,
          forwardingId: 0,
          id: 0,
          isConcern: 0,
          isLike: 0,
          location: "",
          notice: [],
          singerId: 19,
          title: "",
          topicId: 1,
          type: 0,
          updateTime: 0,
          url: [],
          userId: 0,
          version: 1,
          zambiaNum: 0,
        },
        comment: []
      }
    },
    created() {
      document.title = '动态详情';
    },
    mounted() {
      let params = this.$queryParams(location.href);
      // 滚动 tab 吸顶
      window.addEventListener('scroll', this.scrollTopDis);
      this.loadActivitiesData(params);
      this.loadCommentData(params, 0);
      this.loadLikeData(params);
    },
    computed: {
      vPoster: function () {
        if (this.dynamic.type === 3) {
          return this.dynamic.url[0] + '?vframe/jpg/offset/4/h/960'
        }
        if (this.dynamic.type === 4) {
          return this.dynamic.url[0] + '?vframe/jpg/offset/4/h/960'
        }
      }
    },
    methods: {
      loadCommentData: function (params, sortType) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE3 + '/CommentAPI/dynamicCommentList', // 该动态的评论列表
          data: this.$qs.stringify({
            id: params.id,
            page: 0,
            sortType: sortType
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              // this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              let data = res.data;
              this.comment = data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      loadActivitiesData: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE3 + '/dynamic/dynamicDetails',// 动态详情
          data: this.$qs.stringify({
            userId: params.userId,
            id: params.id
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              // this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.dynamic = res.data.data;
              if (this.dynamic.url === null) {
                this.dynamic.url = []
              }
              if (this.dynamic.notice) {
                this.dynamic.title = this.$atSomebody(this.dynamic.notice, this.dynamic.title);
              }
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      loadLikeData: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE3 + '/likeAPI/dynamicLikePageList',// 点赞列表
          data: this.$qs.stringify({
            dynamicId: params.id,
            pageNum: 0,
            pageSize: 5
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              // this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.likeList = res.data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      playVideo: function () {
        let v = document.getElementsByTagName('video')[0];
        this.loadLayer = false;
        this.isPlay = true;
        this.timeTotalText = this.timeCurrentText = '00:00';
        if (v.readyState >= 2) {
          v.play();
        } else {
          this.showWaiting = true
        }
      },
      showTipOffs: function (i) {
        this.$router.push({
          path: '/tipOffs'
        })
      },
      scrollTopDis: function () {
        let winTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let elTop = document.querySelector('.dynamic-comment').offsetTop;
        this.tabFixed = winTop > elTop;
      },
      //图片预览
      previewImage: function (e) {
        ImagePreview({
          images: this.dynamic.url,
          startPosition: e
        })
      },
      chooseSort: function () {
        this.changeSort = true;// 显示选择
      },
      checkedSort: function (e) { // 完成选择
        let params = this.$queryParams(location.href);
        this.changeSort = false;
        switch (e) {
          case 0:
            this.sortStyle = '按热度';
            this.sortIndex = 0;
            this.loadCommentData(params, 0);
            break;
          case 1:
            this.sortStyle = '按时间';
            this.sortIndex = 1;
            this.loadCommentData(params, 1);
            break;
        }
      },
      // 更换排序
      changeTab: function (i) {
        this.changeSort = false;
        switch (i) {
          case 0:
            this.tabIndex = 0;
            break;
          case 1:
            this.tabIndex = 1;
            break;
        }
      }
    },
    destroyed() {
      window.removeEventListener('scroll', this.scrollTopDis)
    }
  }
</script>

