<template>
    <div class="tip-offs">
        <van-radio-group v-model="radio">
            <van-cell-group>
                <van-cell class="cell-list"
                          v-for="(item,index) in list"
                          :title="item"
                          :key="index"
                          clickable
                          @click="radio = index+1">
                    <van-radio checked-color="#B64CEA" :name="index+1">
                        <img
                                class="icon-checked"
                                slot="icon"
                                slot-scope="props"
                                :src="props.checked ? icon.active : icon.normal"
                        >
                    </van-radio>
                </van-cell>
            </van-cell-group>
        </van-radio-group>
        <van-button class="sub-btn" type="primary" round size="large" @click="submitTipOffs"> 提交</van-button>
    </div>
</template>

<script>
  export default {
    name: "TipOffs",
    props: ['tipOffsYes'],
    data() {
      return {
        checked: true,
        icon: {
          normal: require('../../assets/images/weixuanzhong.png'),
          active: require('../../assets/images/xuanzhong.png')
        },
        radio: '',
        list: ['垃圾营销', '敏感信息', '恶意刷屏', '人身攻击', '淫秽色情', '其他举报理由'],
        result: []
      };
    },
    mounted() {
    },
    created(){
      document.title='举报';
    },
    methods: {
      toggle: function (index) {
        this.$refs.checkboxes[index].toggle();
      },
      submitTipOffs: function () {
        if (this.radio === '') {
          this.$toast('请选择一项');
        } else {
          this.$axios({
            method: 'post',
            url: process.env.VUE_APP_BASE1 + '/user_server/tu-user/insertUserComplaint', //举报
            data: this.$qs.stringify({
              userId: 10,
              targetId: 1,
              targetType: 1, //动态：0；评论：1；用户：2；
              type: this.radio
            })
          }).then((res) => {
              console.log(res);
              if (res.data.status !== 1000) {
                this.$toast(res.data.msg);
                return false;
              } else if (res.data.data !== null) {
                let data = res.data.data;
              }
            }
          ).catch((err) => {
            console.log(err);
          });
          this.$toast('举报成功');
          // history.go(-1);
        }
      }
    }
  }
</script>

<style scoped lang="scss">
    @import "../../assets/fonts/iconfont.css";

    .tip-offs {
        position: fixed;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
        z-index: 4;
        background: #fff;
    }

    .van-radio__icon {
        float: right;
    }

    .van-cell__value {
        flex: 0 0 auto;
        width: .5rem;
    }

    .sub-btn {
        position: absolute;
        width: 6rem;
        height: .8rem;
        line-height: .8rem;
        left: 50%;
        bottom: 1rem;
        margin-left: -3rem;
        color: #333333;
        font-size: .3rem;
        background: transparent;
        border: 1px solid #000;
    }

    .cell-list:last-child {
        border-top: .1rem solid #f2f2f2;

    }

    .icon-checked {
        width: .3rem;
        height: .3rem;
        margin-top: .03rem;
    }
</style>
